.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.content {
  overflow-y: auto;
}

.mainContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.text {
  margin: 2rem;
}

.pContainer {
  display: flex;
  flex-direction: column;
  padding: 2.5rem;
  text-align: left;
}

#fontChange {
  font-family: 'Times New Roman';
}

#colorChange {
  color: #33c9dc;
}

#bgChange {
  background-color: #ffe082;
}